body {
margin: 0;
font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
background: #f9f9f9;
color: #333;
}

body.light {
  background: #f9f9f9;
  color: #333;
}

body.dark {
  background: #121212;
  color: #eee;
}
.theme-toggle {
  padding: 8px 12px;
  border: none;
  cursor: pointer;
  border-radius: 6px;
  margin: 10px;
}
.page-container {
padding: 2rem;
max-width: 800px;
margin: auto;
}
.category-badge {
  padding: 3px 6px;
  border-radius: 4px;
  font-size: 12px;
  margin-left: 8px;
}

.category-badge.math { background: #ffd166; }
.category-badge.science { background: #06d6a0; }
.category-badge.history { background: #ef476f; color: white; }
.category-badge.general { background: #118ab2; color: white; }

h1 {
color: #2a4d69;
}


.btn-primary {
background-color: #2a9d8f;
color: #fff;
padding: 0.7rem 1.5rem;
border: none;
border-radius: 6px;
cursor: pointer;
text-decoration: none;
}


.btn-primary:hover {
background-color: #21867a;
}


.form-group {
margin-bottom: 1rem;
}


input {
padding: 0.5rem;
border: 1px solid #ccc;
border-radius: 4px;
width: 100%;
}
.progress-container {
  background: #ddd;
  height: 8px;
  border-radius: 5px;
  margin: 10px 0;
}
.progress-bar {
  background: #06d6a0;
  height: 8px;
  border-radius: 5px;
}
